html {
  font-size: $base-font-size;
}

/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
  margin: 0;
  padding: 0;

}



/**
 * Basic styling
 */
body {
  font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
  color: $text-color;
  background-color: $background-color;
  -webkit-text-size-adjust: 100%;
  -webkit-font-feature-settings: "kern" 1;
     -moz-font-feature-settings: "kern" 1;
       -o-font-feature-settings: "kern" 1;
          font-feature-settings: "kern" 1;
  font-kerning: normal;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  overflow-wrap: break-word;
}



/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
  margin-bottom: $spacing-unit / 2;
}

hr {
  margin-top: $spacing-unit;
  margin-bottom: $spacing-unit;
}

/**
 * `main` element
 */
main {
  min-height: 100%; 
  display: block; /* Default value of `display` of `main` element is 'inline' in IE 11. */
}



/**
 * Images
 */
img {
  max-width: 100%;
  vertical-align: middle;
}



/**
 * Figures
 */
figure > img {
  display: block;
}

figcaption {
  font-size: $small-font-size;
}



/**
 * Lists
 */
ul, ol {
  margin-left: $spacing-unit;
}

li {
  > ul,
  > ol {
    margin-bottom: 0;
  }
}



/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
  font-weight: $base-font-weight;
}



/**
 * Links
 */
a {
  color: $link-base-color;
  text-decoration: none;

  &:visited {
    color: $link-visited-color;
  }

  &:hover {
    color: $link-hover-color;
    text-decoration: underline;
  }

  .social-media-list &:hover {
    text-decoration: none;

    .username {
      text-decoration: underline;
    }
  }
}


/**
 * Blockquotes
 */
blockquote {
  color: $brand-color;
  border-left: 4px solid $border-color-01;
  padding-left: $spacing-unit / 2;
  @include relative-font-size(1.125);
  font-style: italic;

  > :last-child {
    margin-bottom: 0;
  }

  i, em {
    font-style: normal;
  }
}



/**
 * Code formatting
 */
pre,
code {
  font-family: $code-font-family;
  font-size: 0.9375em;
  border: 1px solid $border-color-01;
  border-radius: 3px;
  background-color: $code-background-color;
}

code {
  padding: 1px 5px;
}

pre {
  padding: 8px 12px;
  overflow-x: auto;

  > code {
    border: 0;
    padding-right: 0;
    padding-left: 0;
  }
}

.highlight {
  border-radius: 3px;
  background: $code-background-color;
  @extend %vertical-rhythm;

  .highlighter-rouge & {
    background: $code-background-color;
  }
}



/**
 * Wrapper
 */
.wrapper {
  // max-width: calc(#{$content-width} - (#{$spacing-unit}));
  // margin-right: auto;
  // margin-left: auto;
  // padding-right: $spacing-unit / 2;
  // padding-left: $spacing-unit / 2;
  @extend %clearfix;

  @media screen and (min-width: $on-large) {
    // max-width: calc(#{$content-width} - (#{$spacing-unit} * 2));
    // padding-right: $spacing-unit;
    // padding-left: $spacing-unit;
  }
}



/**
 * Clearfix
 */
%clearfix:after {
  content: "";
  display: table;
  clear: both;
}



/**
 * Icons
 */

.orange {
  color: #f66a0a;
}

.grey {
  color: #828282;
}

.svg-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  fill: currentColor;
  padding: 5px 3px 2px 5px;
  vertical-align: text-bottom;
}


/**
 * Tables
 */
table {
  margin-bottom: $spacing-unit;
  width: 100%;
  text-align: $table-text-align;
  color: $table-text-color;
  border-collapse: collapse;
  border: 1px solid $table-border-color;
  tr {
    &:nth-child(even) {
      background-color: $table-zebra-color;
    }
  }
  th, td {
    padding: ($spacing-unit / 3) ($spacing-unit / 2);
  }
  th {
    background-color: $table-header-bg-color;
    border: 1px solid $table-header-border;
  }
  td {
    border: 1px solid $table-border-color;
  }

  @include media-query($on-laptop) {
    display: block;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
            -ms-overflow-style: -ms-autohiding-scrollbar;
  }
}


body {
      margin: 0;
      padding: 0;
      background: #fdf6e3;
      font: 400 1em/1.4em "IBM Plex Sans", sans-serif;
}

.lines-1 {
      position: absolute;
      display: block;
      top: 0px;
      right: 0;
      width: 60vw;
      height: 400px;
      background: url('../img/line.png') repeat;
      background-position: center center;
      background-size: 400px auto;
      z-index: -1;
}

.lines-2, .flowers-right {
      display: none;
}

.header {
      display: block;
      position: relative;
      width: auto;
      height: 200px;
      color: #3c3c3c;
      z-index: 10;
}

.painting {
      position: absolute;
      top: 0;
      left: 0;
      width: 50px;
      height: 200px;
      background: #fdf6e3 url('../img/untitled_blue.jpg') no-repeat;
      background-size: auto 120%;
      background-position: center left 40%;
}

.intro {
      position: absolute;
      top: 0;
      left: 70px;
      height: 200px;
      padding: 0 20px 0 30px;
      border-left: 1px dotted #3c3c3c;
}

      h1.logotype {
            margin: 0.65em 0 0.35em;
            font: 400 2.3em/1em "IBM Plex Serif", serif;
            line-height: 1em;
      }

      h2.tagline {
            margin: 0.25em 0;
            max-width: 350px;
            font: 400 0.9em/1.8em "IBM Plex Sans", sans-serif;
      }

      h2.tagline a {
            color: #222;
            text-decoration: none;
            border-bottom: 1px dotted #b330ae;
      }

            h2.tagline a:hover {
                  border-bottom: 1px solid #b330ae;
            }

      .subscribe {
            color: #b330ae;
            margin-left: 0px;
            padding: 1px 5px;
            // background: #fffcf2;
            border-radius: 5px;
            font-weight: 500;
      }

            h2.tagline a.subscribe {
                  border: none;
                  // color: #427bbb;
                  color: #2967c3;
                  text-transform: uppercase;
                  font-size: 0.9em;
            }


            h2.tagline a.subscribe img {
              width: 3%;
              margin-bottom: 3px;
            }

nav {
      position: relative;
}

      nav #nav-menu {
            margin: 0;
            padding: 0;
            list-style-type: none;
      }

      nav #nav-menu li {
            display: inline-block;
            width: 25%;
            margin: 0 -2px 0 0;
            font: 500 0.8em/1em "IBM Plex Sans", sans-serif;
            background: #f3e8d3;
            text-transform: uppercase;
            text-align: center;
            border: 1px dotted #3c3c3c;
            // border-left: none;
      }

            nav #nav-menu li:last-child {
                  border-right: none;
            }

            nav #nav-menu li:first-child {
                  border-left: none;
            }

            nav #nav-menu li a {
                  color: #3c3c3c;
                  text-decoration: none;
                  display: inline-block;
                  padding: 22px 0;
                  width: 100%;
                  white-space: nowrap;
            }

            nav #nav-menu li:hover {
                  background: #fdf6e3;
                  border-bottom: none;
            }


            nav #nav-menu .active {
                  background: #fdf6e3;
                  border-bottom: 1px solid #fdf6e3;
            }



                  nav #nav-menu .nav-magenta.active a, nav #nav-menu .nav-magenta:hover a {
                        color: #b330ae;
                  }

                  nav #nav-menu .nav-cyan.active a, nav #nav-menu .nav-cyan:hover a {
                        color: #108597;
                  }

                  nav #nav-menu .nav-yellow.active a, nav #nav-menu .nav-yellow:hover a {
                        color: #d88e00;
                  }

                  nav #nav-menu .nav-blue.active a, nav #nav-menu .nav-blue:hover a {
                        color: #2967c3;
                  }

      .entry {
            position: relative;
            padding: 15px;
            font: 400 1em/1.6em "IBM Plex Sans", sans-serif;
            background: #fdf6e3;
            color: #222;
            border-bottom: 1px dotted #3c3c3c;
            z-index: 10;
      }

            .entry img.headshot {
                  float: left;
                  width: 130px;
                  height: 130px;
                  margin: 5px 20px 10px 5px;
                  border-radius: 120px;
                  border: 8px solid #f3e8d3;
            }

            .entry a {
                  color: #000;
                  text-decoration: none;
                  border-bottom: 1px dotted #b330ae;
            }

                  .entry a:hover {
                        border-bottom: 1px solid #b330ae;
                  }

            .entry h1 {
                  margin: 0.5em 0 1em;
                  font: 300 1.8em/1.2em "IBM Plex Sans", sans-serif;
            }

            .entry h2 {
                  margin: 1.25em 0 0.75em;
                  font: 300 1.6em/1.2em "IBM Plex Serif", serif;
            }

            .entry h3 {
                  margin: 1.25em 0 0.5em;
                  font: 400 1.4em/1.2em "IBM Plex Sans", sans-serif;
            }

            .entry h4 {
                  margin: 1.25em 0 0.25em;
                  font: 500 1.2em/1.2em "IBM Plex Sans", sans-serif;
                  color: #3c3c3c;
            }

            .entry blockquote {
                  margin: 20px 30px;
                  padding: 0 20px;
                  font: 300 1.2em/1.8em "IBM Plex Serif", serif;
                  border-left: 2px solid #108597;
            }

            .entry ul {
                  margin: 20px 0px;
                  padding: 0 40px;
                  list-style-type: square;
            }

                  .entry ul li, .entry ol li {
                        margin: 15px 0px;
                  }

            .entry img {
                  max-width: 100%;
            }

      footer {
            padding: 15px;
            color: #3c3c3c;
            div {
                font-size: small;
                text-align: center;
                a {
                    color: $link-base-color;
                    text-decoration: none;

                    &:visited {
                        color: $link-visited-color;
                    }

                    &:hover {
                        color: $link-hover-color;
                        text-decoration: underline;
                    }

                    .social-media-list &:hover {
                        text-decoration: none;

                        .username {
                            text-decoration: underline;
                        }
                    }
                }
            }

        }

